// General styling
.courses-v23 {
    .courses-v23__related-courses__heading {
        margin-top: $measure--small2;
    }
}

// Slider styles
.js {
    .slider-v23--related-courses__wrap {
        display: grid;
        @include x-column-grid(18);
        align-items: center;
        position: relative;
        overflow: hidden;

        @include media('<tablet') {
            overflow: visible;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        .slider-v23__cards {
            grid-column: 3/-3;
            overflow: visible;

            @include media('<extralarge') {
                grid-column: 2/-2;
            }

            @include media('<medium') {
                grid-column: 2/-2;
            }

            @include media('<tablet') {
                grid-column: 1/-1;
                overflow: hidden;
            }

            &[data-style='responsive']:not(.slider-v23) {
                display: flex;

                .course-card-v23 {
                    width: 50%;

                    @include media('<tablet') {
                        width: 100%;
                    }
                }
            }
        }

        .slider-v23[data-style='responsive'] {
            overflow: visible;
            padding: 0rem;

            @include media('<tablet') {
                margin: 0;
            }

            .slide {
                padding: 0;

                &[data-sliderposition='-1'][data-hidden='true'] {
                    visibility: visible;
                    left: -200%;

                    .course-card-v23__details__wrap {
                        opacity: 0;
                    }
                }

                &.slide--prev {
                    &[data-sliderposition='-1'][data-hidden='true'] {
                        left: -100%;

                        @include media('<tablet') {
                            left: -105%;
                        }
                    }

                    .course-card-v23__details__wrap {
                        opacity: 0;
                    }
                }

                &[data-sliderposition='1'][data-hidden='true'] {
                    visibility: visible;
                    left: 200%;
                    transition: left 1s ease-in-out;

                    .course-card-v23__details__wrap {
                        opacity: 0;
                    }
                }

                &.slide--next {
                    &[data-sliderposition='1'][data-hidden='true'] {
                        z-index: z-index('above');
                        left: 100%;

                        @include media('<tablet') {
                            left: 105%;
                        }
                    }

                    .course-card-v23__details__wrap {
                        animation-duration: 1s;
                        opacity: 0;
                    }
                }
            }

            & + .slider-v23__controls--responsive {
                position: absolute;
                width: 100%;
                display: flex;
                justify-content: space-between;
                z-index: z-index('above');

                @include media('<tablet') {
                    padding-left: 0;
                    padding-right: 0;
                    width: 98vw;
                }

                .slider-v23__indicator {
                    display: none;
                }

                button {
                    &:hover,
                    &:focus,
                    &:active {
                        outline-offset: $border-width-thick-w20;
                    }
                }
            }
        }

        .course-card-v23 {
            padding: $measure--basic;
        }

        .course-card-v23__details__wrap {
            position: absolute;
            padding: $measure--threequarters;
            height: 75%;
            display: block;
            width: 100%;
        }

        .course-card-v23__details {
            background: white;
            height: 100%;
            display: inline-block;
            padding: $measure--small1;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: flex;
            flex-direction: column;
            text-align: center;
            color: $color-black;
        }

        .course-card-v23__details__title {
            font-weight: $font-weight-bold;
            @include font-family(meta);
            font-size: $font-size-gamma;
            margin-bottom: $measure--basic;
            text-decoration: underline;
            line-height: 1.25;
        }

        .course-card-v23__details__text {
            @include font-family(meta);
            text-overflow: ellipsis;
            overflow: hidden;
        }

        .course-card-v23__content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: relative;
            height: 100%;
        }

        .course-card-v23__image {
            position: relative;
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 1); // bracket
    $lead-text: nth($color, 2);
    $accent-color: nth($color, 3); // arrows
    $background-color: nth($color, 8);

    .#{$level}-courses-v23 {
        .courses-v23__related-courses {
            background-color: $background-color;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            z-index: z-index('root');

            &:after {
                content: '';
                position: absolute;
                height: 100%;
                width: 100vw;
                background-color: $background-color;
                top: 0;
                display: flex;
                z-index: z-index('below');
            }

            &:before {
                content: '';
                position: absolute;
                height: $measure--medium9;
                width: 50vw;
                background-color: $color-white-smoke;
                top: 0;
                display: flex;
                z-index: z-index('above');
                left: 50%;
            }
            
            .courses-v23__related-courses__container {
                width: 100%;
            }

            .slider-v23[data-style='responsive'] {
                & + .slider-v23__controls--responsive {
                    button {
                        background: $accent-color;
                        color: $lead-text;
                        width: $measure--medium3;
                        height: $measure--medium3;

                        &:focus,
                        &:active {
                            outline-color: $lead-text;
                        }

                        &:disabled {
                            background: lighten($accent-color, 12%);
                        }
                    }
                }

                .slide {
                    &:focus,
                    &:active {
                        outline-color: $lead-text;
                    }
                }
            }
        }
    }
}
