////
/// accordion-v23
/// @group patterns
////

@mixin accordion() {
    margin-bottom: 0;

    // Heading focus/active needed for Firefox and Safari. Other interaction states on button
    &:focus {
        border: 0;
    }
    &:active {
        border: 0;
        outline: 0;
    }

    button {
        width: 100%;
        text-align: left;
        padding: $measure--basic;
        color: $color-black;
        border-bottom: $border-width-default $border-style-default $color-chinese-silver;

        div {
            grid-column-gap: 0;
            display: flex;
            align-items: center;
            gap: $measure--basic;
        }

        .accordion-v23__heading__indicator {
            background-color: transparent;
            color: $color-black;
            width: $measure--small4;
            height: $measure--small4;
            border-width: 0;
            position: relative;
            cursor: pointer;
            transition: transform 1s ease;
            display: inline-block;
            border: $border-width-default $border-style-default $color-black;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

            &::before,
            &::after {
                content: "";
                background-color: currentColor;
                width: 0.2rem;
                height: $measure--basic;
                display: block;
                position: absolute;
                animation: depressed 0.1s ease both;
                margin: $measure--half;
            }
            
            &::after {
                animation: bar 0.1s ease both reverse;
            }
        }

        &:hover {
            background-color: $color-white;

            & .accordion-v23__heading__text{
                text-decoration: underline;
            }
        }
        &:focus {
            outline: $border-width-default $border-style-default $color-black;
            outline-offset: $border-width-thick;
            background-color: $color-white;
            position: relative;
            z-index: z-index(default);
        }
        &:active {
            outline: $border-width-default $border-style-alternate $color-black;
            outline-offset: $border-width-thick;
            background-color: $color-white;

            & .accordion-v23__heading__text{
                text-decoration: underline;
            }
        }
    }

    &[data-open='true'] {
        margin-top: $measure--basic;
        border-bottom: 0;

        &:focus,
        &:active {
            outline: 0;
        }

        button {
            border: $border-width-default $border-style-default $color-gainsboro;
            border-bottom: none;
            text-decoration: underline;
            background-color: $color-white;

            .accordion-v23__heading__indicator {
                animation: pressed 0.1s ease both;

                &::after {
                    animation: bar 0.1s ease both;
                }
            }
        }

        & + .accordion-v23__body {
            border: $border-width-default $border-style-default $color-gainsboro;
            border-top: none;
            
            p {
                margin: $measure--basic 0;
            }

            & > :first-child {
                margin-top: 0;
            }

            & > :last-child {
                margin-bottom: 0;
            }
        }

        & + .accordion-v23__body:first-child {
            margin-top: 0;
        }
    }
}

.js .accordion-v23 {
    @include media('screen') {

        .accordion-v23__section  {
            &:last-child {
                button {
                    border-bottom: none;
                }
            }

            &[data-open='true'] {
                outline: $border-width-default $border-style-alternate $color-black;
                outline-offset: $border-width-thick;
                //margin-top: $measure--medium9;
            }
        }

        .accordion-v23__body {
            padding: 0 $measure--basic $measure--basic $measure--basic;

            //nested accordions
            .accordion-v23 {
                 .accordion-v23__heading {
                    button {
                        background-color: $color-gallery;
                        border-bottom: $border-width-default solid $color-white;

                        &:hover, &:focus, &:active  {
                            background-color: $color-gallery;
                        }

                        .accordion-v23__heading__indicator {
                            &:before {
                                content: "\f054";
                               @include font-family(icon);
                               height: initial;
                               width: initial;
                               background-color: initial;
                            }
                            &:after {
                                content: none;
                            }
                        }
                    }

                    &[data-open='true'] {
                        button {
                            background-color: $color-gallery;
                            border-bottom: none;
                        }

                        & + .accordion-v23__body {
                            background-color: $color-gallery;
                        }
                    }
                }
            }
        }   

        .accordion-v23__heading {
            @include accordion();

            &[data-open='true'] {
                & + .accordion-v23__body {
                    margin-bottom: $measure--basic;

                    .wrapper--accordion-v23__body__content {
                        & p:first-of-type {
                            margin-top: 0;
                        }
                    }
                }                
            }

            & + .accordion-v23__body {
                overflow-y: hidden;
                transition-duration: $transition-duration;
                transition-property: height;
                transition-timing-function: $transition-function;

                &[data-closed='true'] {
                    display: none;
                }
            }
        }
    }
}

@keyframes pressed {
    from {
      transform: rotateZ(0deg);
    }
    to {
      transform: rotateZ(90deg);
    }
  }
  
  @keyframes depressed {
    0% {
      transform: rotateZ(90deg);
    }
    99.999% {
      transform: rotateZ(180deg);
    }
    100% {
      transform: rotateZ(0deg);
    }
  }
  
  @keyframes bar {
    from {
      transform: rotateZ(-90deg);
    }
    to {
      transform: rotateZ(0deg);
    }
  }