        ('Normal', normal, 400, 'ff-meta/ff-meta-w03-normal'),
        ('Book', normal, 500, 'ff-meta/ff-meta-w03-book'),
        ('Medium', normal, 600, 'ff-meta/ff-meta-w03-medium'),
        ('Bold', normal, 700, 'ff-meta/ff-meta-w03-bold'),
        ('Light Italic', italic, 300, 'ff-meta/ff-meta-w03-light-italic'),
        ('Normal Italic', italic, 400, 'ff-meta/ff-meta-w03-normal-italic'),
        ('Book Italic', italic, 500, 'ff-meta/ff-meta-w03-book-italic'),
        ('Medium Italic', italic, 600, 'ff-meta/ff-meta-w03-medium-italic'),
        ('Bold Italic', italic, 700, 'ff-meta/ff-meta-w03-bold-italic'),
    ),
    'Plex Sans': (
        ('Thin', normal, $font-weight-hairline, 'plex/IBMPlexSans-Thin'),
        ('Extra Light', normal, $font-weight-thin, 'plex/IBMPlexSans-ExtraLight'),
        ('Light', normal, $font-weight-light, 'plex/IBMPlexSans-Light'),
        ('Regular', normal, $font-weight-normal, 'plex/IBMPlexSans-Regular'),
        ('Text', normal, $font-weight-text, 'plex/IBMPlexSans-Text'),
        ('Medium', normal, $font-weight-book, 'plex/IBMPlexSans-Medium'),
        ('Semi Bold', normal, $font-weight-medium, 'plex/IBMPlexSans-SemiBold'),
        ('Bold', normal, $font-weight-bold, 'plex/IBMPlexSans-Bold'),
        ('Thin Italic', italic, $font-weight-hairline,'plex/IBMPlexSans-ThinItalic'),
        ('Extra Light Italic', italic, $font-weight-thin, 'plex/IBMPlexSans-ExtraLightItalic'),
        ('Light Italic', italic, $font-weight-light, 'plex/IBMPlexSans-LightItalic'),
        ('Italic', italic, $font-weight-normal, 'plex/IBMPlexSans-Italic'),
        ('Text Italic', italic, $font-weight-text, 'plex/IBMPlexSans-TextItalic'),
        ('Medium Italic', italic, $font-weight-book,'plex/IBMPlexSans-MediumItalic'),
        ('Semi Bold Italic', italic, $font-weight-medium,'plex/IBMPlexSans-SemiBoldItalic'),
        ('Bold Italic', italic, $font-weight-bold, 'plex/IBMPlexSans-BoldItalic'),
    ),
    'Plex Serif': (
        ('Thin', normal, $font-weight-hairline, 'plex/IBMPlexSerif-Thin'),
        ('Extra Light', normal, $font-weight-thin, 'plex/IBMPlexSerif-ExtraLight'),
        ('Light', normal, $font-weight-light, 'plex/IBMPlexSerif-Light'),
        ('Regular', normal, $font-weight-normal, 'plex/IBMPlexSerif-Regular'),
        ('Text', normal, $font-weight-text, 'plex/IBMPlexSerif-Text'),
        ('Medium', normal, $font-weight-book, 'plex/IBMPlexSerif-Medium'),
        ('Semi Bold', normal, $font-weight-medium, 'plex/IBMPlexSerif-SemiBold'),
        ('Bold', normal, $font-weight-bold, 'plex/IBMPlexSerif-Bold'),
        ('Thin Italic', italic, $font-weight-hairline, 'plex/IBMPlexSerif-ThinItalic'),
        ('Extra Light Italic', italic, $font-weight-thin, 'plex/IBMPlexSerif-ExtraLightItalic'),
        ('Light Italic', italic, $font-weight-light, 'plex/IBMPlexSerif-LightItalic'),
        ('Italic', italic, $font-weight-normal, 'plex/IBMPlexSerif-Italic'),
        ('Text Italic', italic, $font-weight-text, 'plex/IBMPlexSerif-TextItalic'),
        ('Medium Italic', italic, $font-weight-book, 'plex/IBMPlexSerif-MediumItalic'),
        ('Semi Bold Italic', italic, $font-weight-medium,'plex/IBMPlexSerif-SemiBoldItalic'),
        ('Bold Italic', italic, $font-weight-bold, 'plex/IBMPlexSerif-BoldItalic'),
    ),
    'Plex Mono': (
        ('Thin', normal, $font-weight-hairline, 'plex/IBMPlexMono-Thin'),
        ('Extra Light', normal, $font-weight-thin, 'plex/IBMPlexMono-ExtraLight'),
        ('Light', normal, $font-weight-light, 'plex/IBMPlexMono-Light'),
        ('Regular', normal, $font-weight-normal, 'plex/IBMPlexMono-Regular'),
        ('Text', normal, $font-weight-text, 'plex/IBMPlexMono-Text'),
        ('Medium', normal, $font-weight-book, 'plex/IBMPlexMono-Medium'),
        ('Semi Bold', normal, $font-weight-medium, 'plex/IBMPlexMono-SemiBold'),
        ('Bold', normal, $font-weight-bold, 'plex/IBMPlexMono-Bold'),
        ('Thin Italic', italic, $font-weight-hairline,'plex/IBMPlexMono-ThinItalic'),
        ('Extra Light Italic', italic, $font-weight-thin, 'plex/IBMPlexMono-ExtraLightItalic'),
        ('Light Italic', italic, $font-weight-light, 'plex/IBMPlexMono-LightItalic'),
        ('Italic', italic, $font-weight-normal, 'plex/IBMPlexMono-Italic'),
        ('Text Italic', italic, $font-weight-text, 'plex/IBMPlexMono-TextItalic'),
        ('Medium Italic', italic, $font-weight-book, 'plex/IBMPlexMono-MediumItalic'),
        ('Semi Bold Italic', italic, $font-weight-medium,'plex/IBMPlexMono-SemiBoldItalic'),
        ('Bold Italic', italic, $font-weight-bold, 'plex/IBMPlexMono-BoldItalic'),
    ),
);

/// @type Map
/// @prop {String} family - Font Family
/// @prop {List} types - Font File Types
$font-families-types: (
    'FF Meta': (
        woff2,
        woff,
    ),  
    'Plex Sans': (
        woff2,
        woff,
    ),
    'Plex Serif': (
        woff2,
        woff,
    ),
    'Plex Mono': (
        woff2,
        woff,
    ),
);

@each $family, $styles in $font-families-styles {
    @each $style in $styles {
        $src: ();
        $src: append($src, local('#{$family} #{nth($style, 1)}'));
        @each $type in map-get($font-families-types, $family) {
            $url: asset-url('/fonts/#{nth($style, 4)}.#{$type}')
                format('#{$type}');
            $src: append($src, $url, comma);
        }

        @font-face {
            font-family: $family;
            font-style: nth($style, 2);
            // stylelint-disable font-weight-notation
            font-weight: nth($style, 3);
            // stylelint-enable font-weight-notation
            // font-display: $font-display;
            src: #{$src};
            unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA,
                U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF,
                U+EFFD, U+F000;
        }
    }
}

$fa-font-path: '../fonts/fa4';
@import '../../node_modules/font-awesome/scss/font-awesome';
@import '../generated/icons.generated';
