/*
* Styles for arrow and responsive slider
*/

.js {
    .slider-v23[data-style='responsive'] {
        display: flex;
        margin-left: 0;
        margin-right: 0;
        overflow: hidden;
        margin: $measure--basic 0;
        position: relative;

        @include media('<between') {
            margin: $measure--basic;
        }

        @include media('>=between') {
            
            &[data-style='buttons'] {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
            }
        }

        &[data-optimised='true'] {
            li {
                margin-top: 0;

                ul li {
                    a {
                        height: 100%;
                    }
                }
            }
        }
        
        .slide {
            top: 0;
            transition: left 1s ease-in-out;
            width: 100%;
            list-style: none;
            padding: $border-width-thick;
            margin-top: 0;
            max-width: inherit;

            /*
            @include media('reducemotion') {
                transition: none;
            }
            */

            ul {
                display: flex;
                max-width: 100%;
                grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
                list-style-type: none;
                margin: $measure--basic 0;

                @include media('<tablet') {
                    margin: $measure--basic 0;
                }

                li {
                    flex-basis: 50%;

                    &:nth-child(2) {
                        margin-bottom: 0;
                    }

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }

            &:focus {
                @include outline-properties($color-black);
                outline-offset: -#{$border-width-thick};
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            &[data-sliderposition='-1'] {
                left: -100%;
                position: absolute;

                &[data-smallhidden='true'] {
                    visibility: hidden;
                }
            }

            &[data-sliderposition='0'] {
                left: 0;
                position: relative;
            }

            &[data-sliderposition='1'] {
                left: 100%;
                position: absolute;

                &[data-smallhidden='true'] {
                    visibility: hidden;
                }
            }
        }

        // Allow space for box-shadow to display fully. On smaller screens removed to align with parent container's left/right margin/padding
        @include media('>=tablet') {
            padding: $measure--basic;
        }
      
        //  Edge case example to hide controls is responsive slider present with 1 slides containing 2 items
        &[data-count='1']:not([data-small='true']) {
            @include media('>=tablet') {
                & + .slider-v23__controls {
                    display: none;
                }
            }
        }

        & + .slider-v23__controls {
            button {
                border: none;
            }
        }
    }

    // Edge case: responsive/arrow slider with 1 item where slider doesn't load
    .slider-v23__cards[data-style='responsive']:not(.slider-v23) {
        @include media('>=tablet') {
            justify-content: initial;
            grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
            grid-gap: $measure--small2;      
        }
    }
}
