.courses-v23__apply__wrapper{

    @include media('>medium'){
        position: relative;
        padding-left: $measure--small4;
        padding-right: $measure--small4;
        padding-bottom: $measure--medium3;
        padding-top: $measure--medium3;
    }

    @include media('<=medium'){
        padding-bottom: $measure--medium3;
    }
}

.courses-v23__apply {
    position: relative;
    margin-top: $measure--small4;

    
    @include bracket-block($color-white);

    .bracket-block__anglesB {
        &:before {
            width: $measure--huge5;
            border-width: $measure--medium11 $measure--medium11 0 0;
        }

        &:after {
            width: $measure--huge5;
            border-width: 0 $measure--medium11 $measure--medium11 0;
        }

        @include media('<=medium'){
            &:after,
            &:before{
                right: -$measure--medium11;
            }

            &:after{
                width: $measure--huge1;
                border-width: 0 $measure--medium3 $measure--medium3 0;
            }

            &:before{
                width: $measure--huge1;
                border-width: $measure--medium3 $measure--medium3 0 0;
            }
        }
    }

    .courses-v23__apply__promo-content {
        position: relative;
        margin-top: $measure--small4;
        margin-bottom: $measure--small4;
        padding-top: $measure--small4;
        padding-right: $measure--medium1;
        padding-left: $measure--medium1;

        @include media('>medium') {
            padding-top: $measure--medium3;
            padding-left: $measure--medium11;
            padding-bottom: $measure--medium3;
            padding-right: $measure--huge5;

            .courses-v23__apply__promo-content__cta-group {
                display: flex;
                column-gap: $measure--basic;
                justify-content: space-between;
            }
        }

        @include media('<=medium') {
            .courses-v23__apply__promo-content__cta:nth-child(n + 2) {
                margin-top: $measure--basic;
            }
        }
    }

    .courses-v23__apply__right-bracket {
        position: relative;

        svg {
            transform: rotate(180deg);
            position: absolute;
            height: 101%;
            width: auto;
            right: 0;
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 8);
    $lead-text: nth($color, 2);
    $accent-color: nth($color, 3);
    $text-inverted-color: nth($color, 16);
    $anchor-highlight: nth($color, 16);

    .#{$level}-courses-v23 {
        .courses-v23__apply {
            color: $lead-text;

            @include media('<=medium'){
                background-color: $lead-color;
            }

            .courses-v23__apply__wrapper{
                background-color: $lead-color;
            }

            a {
                color: $lead-text;
                border-bottom-color: $lead-text;

                &:hover {
                    color: $anchor-highlight;
                    background-color: $lead-text;
                }

                &:active,
                &:focus {
                    outline-offset: $border-width-thick-w20;
                    outline-color: $lead-text;
                }
            }

            .courses-v23__apply__cta {
                @include anchor(cta--outline-v23, $lead-text);

                &:hover,
                &:focus,
                &:active {
                    color:  $text-inverted-color;
                }
            }

            .courses-v23__apply__promo-content {
                @include bracket-block($accent-color);

                @include media('<=medium'){
                    .bracket-block__anglesA{
                        &:after,
                        &:before{
                            left: 0;
                        }
                    }
                }
            }

            @if ($level=='pg') {
                .courses-v23__apply__promo-content__cta {
                    width: 100%;
                    @include anchor(cta--white);

                    &:active,
                    &:focus{
                        outline-color: $color-white;
                    }
                }
            } @else {
                .courses-v23__apply__promo-content__cta {
                    width: 100%;
                    @include anchor(cta--black);

                    &:active,
                    &:focus{
                        outline-color: $color-black;
                    }
                }
            }
        }
    }
}
