.courses-v23__careers {
    @include media('>medium'){
        @include x-column-grid(18);
        display: grid;
    }

    .courses-v23__careers__heading {
        grid-column: 1/-1;
    }

    .courses-v23__careers__content {
        grid-column: 1/13;

        @include media('<large'){
            grid-column: 1/12;
        }
    }

    .courses-v23__careers__alumni {
        grid-column: 13/-1;

        @include media('<large'){
            grid-column: 12/-1;
        }

        .slider-v23__container {
            padding: $measure--small2;
            position: relative;
        }
    } 
}