@import 'courses-v23/fee';
@import 'courses-v23/pie-chart';
@import 'courses-v23/key-info';
@import 'courses-v23/overview';
@import 'courses-v23/student-life';
@import 'courses-v23/apply-promo-area';
@import 'courses-v23/hallway';
@import 'courses-v23/related-courses';
@import 'courses-v23/entry-requirements';
@import 'courses-v23/study-abroad';
@import 'courses-v23/contact';
@import 'courses-v23/careers';

.courses-v23 {
    section,
    > div {
        @include full-screen;
    }

    p,
    li {
        @include content-style();
    }

    h2:not(.accordion-v23__heading),
    h3:not(.accordion-v23__heading),
    h4:not(.accordion-v23__heading),
    a:not(.accordion-v23__heading) {
        max-width: $max-line-length;
    }

    table {
        margin: 0;
    }

    .accordion-v23 {
        margin-top: $measure--small1;
        margin-bottom: $measure--small1;
    }
}
.courses-v23__grid-18 {
    @include media('>medium') {
        display: grid;
        @include x-column-grid(18);
    }
}
.courses-v23__title {
    h1 {
        margin: $measure--basic 0 0 0;
    }
}

.courses-v23__structure {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__structure__content {
            grid-column: 1/13;
        }

        .courses-v23__structure__image {
            grid-column: 13/-1;
        }

        .accordion-v23 {
            grid-column: 1/13;
        }
    }
}

.courses-v23__study-abroad {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__study-abroad__content, .courses-v23__study-abroad__heading {
            grid-column: 1/13;
        }

        .courses-v23__study-abroad__image {
            grid-column: 13/-1;
        }
    }
}

.courses-v23__placements {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__placements__content {
            grid-column: 1/13;
        }

        .courses-v23__placements__image {
            grid-column: 13/-1;
        }
    }
}

.courses-v23__careers {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;
    }

    .courses-v23__careers__heading {
        grid-column: 1/-1;
    }

    .courses-v23__careers__content {
        grid-column: 1/13;
    }

    .courses-v23__careers__alumni {
        grid-column: 13/-1;

        .slider-v23__container {
            padding: $measure--small2;
            position: relative;
        }
    }
}

.courses-v23__side-image-mobile-hidden {
    @include media('<=medium') {
        display: none;
    }
}

@each $level, $color in $courses-v23-palette {
    $decoration: nth($color, 5);

    .#{$level}-courses-v23 {
        .courses-v23__structure__content ul {
            @include custom-unordered-list('\f00c', $decoration);
        }
    }
}
